//variaveis do grid
$l-device: 1152px;
$m-device: 760px;
$s-device: 480px;

// Mixin de media-queries
@mixin d($size) {
	@if $size == l {
		@media screen and (min-width: #{$l-device}) {
			@content;
		}
	}
	@else if $size == m {
		@media screen and (min-width: #{$m-device}) and (max-width: #{$l-device - 1}) {
			@content;
		}
	}
	@else if $size == s {
		@media screen and (max-width: #{$m-device - 1}) {
			@content;
		}
	}
}